import * as R from 'ramda';
import axios from 'axios';
import FileSaver from 'file-saver';
import { rules } from '@sis-cc/dotstatsuite-components';
import { structureParser, getConfig, getFilename, getFusionConfig } from '../lib/sdmx';
import { sdmxRange } from '../lib/settings';
import * as fm from './formatMapper';

const getLocale = R.pathOr('en', ['locale', 'id']);
const keysLimit = 800;

const getStructure = ({ configArgs }) => {
  const { url, datasource } =
    R.prop('backendId', configArgs.dataflow) == 'FUSION'
      ? getFusionConfig({ configArgs })
      : getConfig({ type: 'structure' })({ configArgs });

  const qStringParams =
    R.prop('backendId', configArgs.dataflow) == 'FUSION'
      ? { references: 'all', format: 'sdmx-json', detail: 'structureOnly', includeMetrics: true }
      : { references: 'all' };

  const acceptParam =
    R.prop('backendId', configArgs.dataflow) == 'FUSION'
      ? {
          'Accept-Language': getLocale(configArgs),
        }
      : {
          Accept: 'application/vnd.sdmx.structure+json;version=1.0',
          'Accept-Language': getLocale(configArgs),
        };

  const mapSDMX_structure = data => {
    return data.data;
  };
  const mapSDMX_structureFusion = data => {
    return fm.mapStructure(data.data, configArgs.locale.id);
  };

  const sdmxMapperStruct =
    R.prop('backendId', configArgs.dataflow) == 'FUSION'
      ? mapSDMX_structureFusion
      : mapSDMX_structure;

  return (
    axios
      .get(url, {
        params: R.cond([
          [
            R.always(R.prop('supportsReferencePartial', datasource)),
            R.assoc('detail', 'referencepartial'),
          ],
          [R.T, R.identity],
        ])(qStringParams),
        headers: acceptParam,
      })
      //.then(R.pipe(R.prop('data'), structureParser));
      .then(R.pipe(sdmxMapperStruct, structureParser))
  );
};

const getData = ({ configArgs, requestArgs }) => {
  const { url, datasource } = getConfig({ type: 'data' })({ configArgs });

  const limitData = data => {
    const dataNode = data.data;
    const keys = Object.keys(dataNode.dataSets[0].observations);

    dataNode.dataSets[0].limited = {
      returnedKeyCount: keys.length,
      limitedTo: keysLimit,
    };
    if (keys.length > keysLimit) {
      for (let i = keysLimit; i < keys.length; i++)
        delete dataNode.dataSets[0].observations[keys[i]];
    }
  };

  const mapSDMX_data = data => {
    limitData(data);
    return data.data;
  };
  const mapSDMX_dataFusion = data => {
    limitData(data.data);
    return fm.mapData(data.data, configArgs.locale.id);
  };

  const sdmxMapperData =
    R.prop('backendId', configArgs.dataflow) == 'FUSION' ? mapSDMX_dataFusion : mapSDMX_data;

  return (
    axios
      .get(url, {
        params: R.mergeRight({ dimensionAtObservation: 'AllDimensions' }, requestArgs),
        headers: R.cond([
          [
            R.always(R.prop('hasRangeHeader', datasource)),
            R.assoc('Range', `values=${R.head(sdmxRange)}-${R.last(sdmxRange)}`),
          ],
          [R.T, R.identity],
        ])({
          Accept: 'application/vnd.sdmx.data+json;version=1.0.0-wd',
          'Accept-Language': getLocale(configArgs),
        }),
      })
      //.then(R.pipe(R.prop('data'), rules.v8Transformer));
      .then(R.pipe(sdmxMapperData, rules.v8Transformer))
  );
};

const getDataFile = ({ configArgs, requestArgs }) => {
  const { url } = getConfig({ type: 'data' })({ configArgs });

  let accept = 'application/vnd.sdmx.data+csv;file=true';
  if (configArgs.dataflow.backendId && configArgs.dataflow.backendId == 'FUSION') {
    requestArgs['format'] = 'csv-ts';
    requestArgs['labels'] = 'name';
    accept = '';
  }

  return axios
    .get(url, {
      params: requestArgs,
      //params:{format:"csv",labels:"name"},
      headers: {
        Accept: accept,
        'Accept-Language': getLocale(configArgs),
      },
      responseType: 'blob',
    })
    .then(response => {
      const blob = new Blob([R.prop('data')(response)], {
        type: R.pathOr('text/csv', ['headers', 'content-type'])(response),
      });
      FileSaver.saveAs(blob, `${getFilename(configArgs)}.csv`);
    });
};

const methods = { getStructure, getData, getDataFile };

const error = method => () => {
  throw new Error(`Unkown method: ${method}`);
};
const main = ({ method, ...rest }) => (methods[method] || error(method))(rest);
R.compose(R.forEach(([name, fn]) => (main[name] = fn)), R.toPairs)(methods);

export default main;
